<template>
  <div class="main-container">
    <div class="inner-container">
      <div class="login-container">
        <div class="image-container">
          <div class="logo text-h5">
            Aravind Eye Care
          </div>
        </div>
        <div class="login-form">
          <div class="login-header text-h6">Login</div>
          <div class="input-container">
            <v-text-field
              type="text"
              placeholder="User Name"
              v-model="userName"
              :error-messages="userNameErrors"
              solo
            >
              <v-icon slot="prepend-inner">mdi-account</v-icon>
            </v-text-field>
            <v-text-field
              type="password"
              placeholder="Password"
              v-model="password"
              solo
            >
              <v-icon slot="prepend-inner">mdi-form-textbox-password</v-icon>
            </v-text-field>
          </div>
          <v-btn
            class="login-button"
            color="rgb(32, 106, 93)"
            rounded
            @click="login()"
            >Login</v-btn
          >
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Login",
  data: function () {
    return {
      userName: "",
      password: "",
      userNameErrors: undefined,
    };
  },
  watch: {},
  methods: {
    login() {
      if (this.userName && this.password) {
        const body = {
          criteria: {
            userName: this.userName,
            password: this.password,
          },
        };
        this.$store.dispatch("login", body).then(() => {
          this.$router.push("/");
        });
      }
    },
  },
};
</script> 

